    -webkit-justify-content: space-around;
            justify-content: space-around;
  }

  
  
  .fa {
    margin-left: -2.5rem;
    cursor: pointer;
    pointer-events: auto;
    z-index: 10;
    background-color: #fff;
    background-image: none;
    color: #666;
    transition: color 0.15s linear;
    font-size: 2rem;
  }
  .fa:hover {
    color: rgb(47, 214, 122);
  }
  
  .fa-plus:before {
    content: "\f067";
